body {
  font-family: Arial, sans-serif;
  margin: 0;
  padding: 0;
}

button {
  cursor: pointer;
  padding: 8px 12px;
  border: none;
  background-color: #4caf50;
  color: white;
  border-radius: 4px;
}

button:disabled {
  background-color: grey;
  cursor: not-allowed;
}

.product-card {
  border: 1px solid #ddd;
  padding: 10px;
  border-radius: 5px;
  width: 150px;
  text-align: center;
}

.product-grid {
  display: flex;
  gap: 20px;
  flex-wrap: wrap;
}

table, th, td {
  border: 1px solid #ddd;
  padding: 10px;
}

th {
  background-color: #f2f2f2;
}
